.wp-block-popupblocks-popup-blocks {
	background-color: #21759b;
	color: #fff;
	padding: 2px;
}

.block-editor-url-popover__input-container textarea {
  height: 75px;
  width: 200px;
}

#editor .popupblocks-tooltip {
  background-color: #afafaf;
}

.popupblocks-tooltip svg {
  height: 18px;
}

.wp-dark-mode-active .popupblocks-tooltip svg {
  fill: white;
}

.popupblocks-tooltip.popupblocks-noicon::after {
  content:"";
}

.entry-content .modal-dialog h5 {
	margin-bottom: 0px;
}

.sticky-modal-header {
  position: sticky;
  top: 0;
  background-color: inherit; /* [1] */
  z-index: 1055; /* [2] */
}


.sticky-modal-footer {
  position: sticky;
  bottom:0;
  background-color: inherit; /* [1] */
  z-index: 1055; /* [2] */
}

.dynamic-container {
  position:relative;
}

.htmx-request.htmx-indicator.indicator-full {
  position: absolute;
  height: 100%;
  width: 100%;
  text-align:center;
  z-index: 1070;
}
